.container{padding:20px;max-width:1200px;margin:0 auto}.title{margin-bottom:30px;font-size:24px;font-weight:600}.form{display:flex;flex-direction:column;gap:20px}.formGroup{display:flex;flex-direction:column;gap:8px}.label{font-weight:500;color:#333}.input{padding:8px 12px;border:1px solid #ddd;border-radius:4px;font-size:16px}.input:focus{outline:none;border-color:#2196f3}.editor{margin-bottom:20px}.editor :global(.ql-toolbar){border-top-left-radius:4px;border-top-right-radius:4px;background:#f8f9fa;border:1px solid #ddd}.editor :global(.ql-container){height:400px;font-size:16px;border:1px solid #ddd;border-top:none;border-bottom-left-radius:4px;border-bottom-right-radius:4px}.editor :global(.ql-editor){min-height:400px}.buttonGroup{display:flex;gap:12px;margin-top:20px}.submitButton,.cancelButton{padding:10px 20px;border:none;border-radius:4px;font-size:16px;cursor:pointer;transition:background-color .2s}.submitButton:disabled,.cancelButton:disabled{opacity:.7;cursor:not-allowed}.submitButton{background-color:#2196f3;color:#fff}.submitButton:hover:not(:disabled){background-color:#1976d2}.cancelButton{background-color:#f5f5f5;color:#333}.cancelButton:hover{background-color:#e0e0e0}.quillLoading{padding:20px;text-align:center;background:#f5f5f5;border-radius:4px}